import { FiltersType } from "../../BLL/Reducers/rimsReducer";


type PropsType={
    filters:FiltersType
    setAccessory:(filters:FiltersType,page:number)=>void
}

let AccessoryFilters:React.FC<PropsType>=(props)=>{
    let onChange=(e:any)=>{
        props.setAccessory({...props.filters,sort_by_price:e.value},1)
    }
    return<div className="right">
        <div className="">
            <select className="form-control" onChange={onChange}>
                <option value={"null"}>
                    Сначала новые
                </option>
                <option value={"true"}>
                    Сначала Подешевле
                </option>
                <option value={"true"}>
                    Сначала Подороже
                </option>
            </select>
        </div>
    </div>
}

export default AccessoryFilters